import React from 'react'
import {NavLink} from "react-router-dom";

const Workoutheader = () => {
    return (
        <>
           <header className="workout-header">
               <section className="workout-sub-header">
                 
                   <h1 className="text-center my-5"> WORKOUTS</h1>

                   <div className="container">
                      <div className="row">
                        <div className="col">
                          
                        <div className="card" >
                           <img src="images/exercise.jpg" className="card-img-top" alt="..." />
                           <div className="card-body">
                             <h5 className="card-title"> Weight training</h5>
                             <p className="card-text">find various weight training exercises according to muscle groups</p>
                             <NavLink to="/weighttraining" className="btn  btn-style">Explore</NavLink>
                           </div>
                         </div>
                        </div>

                        <div className="col">                         
                        <div className="card">
                            <img src="images/yoga2.png" className="card-img-top" alt="..." />
                            <div className="card-body">
                              <h5 className="card-title">Yoga</h5>
                              <p className="card-text">Learn various types of yoga forms and types of  meditation</p>
                              <NavLink to="/yoga" className="btn  btn-style">Explore</NavLink>
                            </div>
                          </div>
                        </div>

                        <div className="col">
                        <div className="card">
                            <img src="images/cardio-2.png" className="card-img-top" alt="..." />
                            <div className="card-body">
                              <h5 className="card-title">Cardio</h5>
                              <p className="card-text">Learn about cardiac health and all types of  cardio exercises</p>
                              <NavLink to="/cardio" className="btn  btn-style">Explore</NavLink>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  
               </section>
               </header> 

               <div>
              
      <form className=" bg-white shadow-md rounded px-8 pt-6 pb-8 mb-4" action="http://127.0.0.1:5000">
        <h1 className="text-center main-heading mt-5">Predict Calories Burned in your workout</h1>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Gender
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Enter 0 for female and 1 for male"
          />
        </div>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Age
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Type your age"
          />
        </div>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Height
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Height in cm"
          />
        </div>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Weight
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Weight in kg"
          />
        </div>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Duration of exercise
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Duration of activity in minutes"
          />
        </div>
        <div className="mb-4">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="username"
          >
            Heart rate
          </label>
          <input className="shadow appearance-none border rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Height "
            type="text"
            placeholder="Your heart rate"
          />
        </div>
        <div className="mb-6">
          <label
            className="block text-gray-700 text-sm font-bold mb-2"
            for="password"
          >
            Body Temperature 
          </label>
          <input
            className="shadow appearance-none border  rounded w-full py-2 px-3 text-gray-700 mb-3 leading-tight focus:outline-none focus:shadow-outline"
            id="Weight"
            type="text"
            placeholder="Temperature is celcious"
           />
        </div>
        <div className="d-flex align-items-center justify-content-center">
          <button
            className="btn-style btn-style-border"
            type="button"
           
          >
            Calculate BMI
          </button>
        </div>
       
      </form>
               </div>
        </>
    )
}

export default Workoutheader
